import words from './words.json';

// validGuess returns true if and only if the guess is valid. In order for a
// guess to be valid, it must be in the word list.
export function validGuess(guess) {
  return guess && words.indexOf(guess) > -1;
}

// generateWord returns a new random word from the wordlist.
export function generateWord() {
  const max = words.length;
  const idx = Math.floor(Math.random() * max);
  return words[idx];
}

// check returns an array of letter objects, one for each letter in the guess.
// In addition to containing the guessed letter, the letter objects also
// annotate whether the letter is in the correct position for the solution, and
// whether the letter is found in the solution.
export function check(guess, solution) {
  // Build an initial result array, with one element per letter guessed. We'll
  // set inWord and rightPlace later, but for now initialize them to false.
  const result = guess
    .split('')
    .map(letter => ({
      letter,
      inWord: false,
      rightPlace: false
    }));

  // Make a list of all the letters in the solution that haven't been "used"
  // yet. For each letter present in the guess that is also present in the
  // solution, the letter will be removed from remaining. This way, we only
  // show the correct number of letters as "present in the wrong place" in case
  // the guess or solution contains doubles.
  let remaining = solution.split('');

  // First, look for letters that are in the right place.
  for (let i = 0; i < 5; i++) {
    const guessedLetter = guess[i];
    if (guessedLetter === solution[i]) {
      // Set the flags on the letter object we're returning, and remove one
      // copy of this letter from the remaining pool.
      result[i].inWord = true;
      result[i].rightPlace = true;
      remaining = remove(remaining, guessedLetter);
    }
  }

  // Next, check remaining letters to see if there's a match.
  for (let i = 0; i < 5; i++) {
    // Skip any letters that have already been found to be in the correct
    // position.
    if (result[i].rightPlace) {
      continue;
    }
    const guessedLetter = guess[i];
    if (contains(remaining, guessedLetter)) {
      // There is an instance of guessedLetter somewhere in the word, but we
      // know that it's not in the correct position because our first iteration
      // didn't mark it.
      //
      // Mark the letter as being in the word, and remove it from the remaining
      // pool.
      result[i].inWord = true;
      remaining = remove(remaining, guessedLetter);
    }
  }

  return result;
}

// contains returns true if and only if the pool contains letter.
function contains(pool, letter) {
  return pool.indexOf(letter) > -1;
}

// remove returns the pool with at most one instance of letter removed. If pool
// does not contain any instances of letter, then the original pool is
// returned.
function remove(pool, letter) {
  const pos = pool.indexOf(letter);
  if (pos > -1) {
    return pool.filter((x, idx) => idx !== pos);
  }
  return pool;
}
